.movie-image-icon {
  height: 405px;
  width: 275px;
  position: absolute;
  margin: 0 !important;
  bottom: -85px;
  left: calc(50% - 137.5px);
  border-radius: 50px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.movie-details,
.movie-image-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  box-sizing: border-box;
}
.movie-image-wrapper {
  height: 299px;
  flex: 1;
  border-radius: 45px;
  border: 1px solid #000;
  overflow: hidden;
  justify-content: flex-start;
  position: relative;
  flex-shrink: 0;
  cursor: pointer;
  z-index: 1;
}
.movie-details {
  width: 271px;
  justify-content: flex-end;
  padding: 0 35px;
}
.wonder-woman {
  margin: 0;
}
.wonder-woman-5410-container {
  height: 69px;
  flex: 1;
  position: relative;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  z-index: 2;
}
.movie-title-container {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 14px 0 0;
  margin-top: -1.5px;
}
.coming-soon {
  width: 236px;
  position: relative;
  font-size: 36px;
  font-weight: 700;
  font-family: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 1;
  margin: -1.5px 0 0;
}
.movies-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 290px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
}
.shows-container {
  align-self: stretch;
  height: 409px;
  position: relative;
  text-align: center;
  font-size: 24px;
  color: #fff;
  font-family: "Inria Sans";
}
@media screen and (max-width: 750px) {
  .coming-soon {
    font-size: 29px;
  }
}
@media screen and (max-width: 450px) {
  .wonder-woman-5410-container {
    font-size: 19px;
  }
  .coming-soon {
    font-size: 22px;
  }
}
