.card-number-field {
  position: absolute;
  top: 63.5px;
  left: 107px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #c5c6d0;
  border: 1px solid #fff;
  box-sizing: border-box;
  width: 1078px;
  height: 102px;
}
.card-number {
  position: absolute;
  top: 0;
  left: 0;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 540px;
  height: 68.1px;
}
.card-info {
  width: 1185px;
  height: 165.5px;
  position: relative;
  max-width: 100%;
}
.cvv-input-field {
  height: 102px;
  width: 286px;
  position: absolute;
  margin: 0 !important;
  right: 34px;
  bottom: -82px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #c5c6d0;
  border: 1px solid #fff;
  box-sizing: border-box;
}
.cvv,
.cvv-input {
  flex: 1;
  display: flex;
  position: relative;
  max-width: 100%;
}
.cvv {
  height: 85px;
  font-weight: 600;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.cvv-input {
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  min-width: 351px;
}
.expiry-date,
.expiry-date-input {
  align-self: stretch;
  position: relative;
  flex-shrink: 0;
}
.expiry-date {
  height: 85px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}
.expiry-date-input {
  width: 286px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #c5c6d0;
  border: 1px solid #fff;
  box-sizing: border-box;
}
.expiry-date-field {
  width: 494px;
  height: 102px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 20px;
  box-sizing: border-box;
  max-width: 100%;
  margin-top: -20px;
}
.cvv-date,
.cvv-input-parent,
.expiry {
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.expiry {
  flex: 1;
  flex-direction: column;
  align-items: flex-end;
  min-width: 351px;
  margin-left: -101px;
}
.cvv-date,
.cvv-input-parent {
  flex-direction: row;
  align-items: flex-start;
}
.cvv-input-parent {
  flex: 1;
  row-gap: 20px;
}
.cvv-date {
  align-self: stretch;
  padding: 0 0 0 22px;
  box-sizing: border-box;
}
.change-of-payment {
  width: 798px;
  position: relative;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  text-shadow: 1px 0 0#fff, 0 1px 0#fff, -1px 0 0#fff, 0-1px 0#fff;
  max-width: 100%;
}
.cvv-expiry,
.primary-button-wrapper1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.cvv-expiry {
  width: 1001px;
  max-width: 100%;
}
.primary-button-wrapper1 {
  width: 178px;
  padding: 77px 0 0;
  box-sizing: border-box;
  font-size: 32px;
  color: #fff;
}
.payment-input-fields {
  width: 1264px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 100%;
  gap: 20px;
}
.body,
.payment-details1 {
  display: flex;
  justify-content: flex-start;
  max-width: 100%;
}
.payment-details1 {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  font-size: 36px;
}
.body {
  width: 1340px;
  flex-direction: column;
  align-items: flex-end;
  padding: 0 20px 0 0;
  box-sizing: border-box;
  gap: 1px;
  text-align: center;
  font-size: 48px;
  color: #000;
  font-family: "IBM Plex Sans Thai";
}
@media screen and (max-width: 1425px) {
  .payment-input-fields {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 1200px) {
  .cvv-input-parent {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 825px) {
  .card-number {
    font-size: 38px;
  }
  .cvv {
    font-size: 29px;
  }
  .cvv-input {
    min-width: 100%;
  }
  .expiry-date {
    font-size: 29px;
  }
  .expiry {
    min-width: 100%;
    margin-left: 0;
  }
  .change-of-payment {
    font-size: 29px;
  }
}
@media screen and (max-width: 450px) {
  .card-number {
    font-size: 29px;
  }
  .change-of-payment,
  .cvv,
  .expiry-date {
    font-size: 22px;
  }
}
