.payment-button-background {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: red;
  border: 1px solid #fff;
  box-sizing: border-box;
}
.pay-now,
.primary-button2 {
  display: flex;
  position: relative;
}
.pay-now {
  height: 56px;
  flex: 1;
  font-weight: 600;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.primary-button2 {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  white-space: nowrap;
  cursor: pointer;
  z-index: 2;
  text-align: center;
  font-size: 24px;
  color: #fff;
  font-family: "IBM Plex Sans Thai";
}
