.empty-gallery-item {
  height: 299px;
  flex: 1;
  position: relative;
  border-radius: 50px;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
}
.gallery-item,
.gallery-sub-item {
  flex-shrink: 0;
  display: flex;
  align-items: flex-start;
}
.gallery-sub-item {
  align-self: stretch;
  border-radius: 45px;
  background-color: #fff;
  border: 1px solid #fff;
  overflow: hidden;
  flex-direction: row;
  justify-content: flex-start;
  cursor: pointer;
  z-index: 1;
}
.gallery-item {
  width: 201px;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 0 17px;
  box-sizing: border-box;
}
.frame-child2,
.gallery-inner {
  align-self: stretch;
  flex-shrink: 0;
}
.frame-child2 {
  flex: 1;
  position: relative;
  border-radius: 30px;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  z-index: 1;
}
.gallery-inner {
  width: 275px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 17px;
  box-sizing: border-box;
}
.gallery-item-child {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: 30px;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  flex-shrink: 0;
  cursor: pointer;
  z-index: 1;
}
.gallery-item1 {
  height: 316px;
  width: 201px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 17px;
  box-sizing: border-box;
}
.card-img-icon2 {
  height: 299px;
  width: 201px;
  position: relative;
  border-radius: 45px;
  flex-shrink: 0;
  object-fit: cover;
}
.gallery {
  height: 422px;
  width: 763px;
  overflow-x: auto;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 104.3px;
  min-width: 763px;
  max-width: 100%;
  margin-left: -15px;
}
@media screen and (max-width: 1350px) {
  .gallery {
    flex: 1;
    gap: 52px;
    margin-left: 0;
  }
}
@media screen and (max-width: 1125px) {
  .gallery {
    min-width: 100%;
  }
}
@media screen and (max-width: 800px) {
  .gallery {
    gap: 26px;
  }
}
