.button-shape1 {
  height: 50px;
  width: 150px;
  position: absolute;
  margin: 0 !important;
  right: -30px;
  bottom: -13px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: red;
  border: 1px solid #fff;
  box-sizing: border-box;
}
.book-now1,
.primary-button3 {
  display: flex;
  position: relative;
  z-index: 1;
}
.book-now1 {
  margin-left: -5px;
  height: 25px;
  width: 119px;
  font-weight: 600;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
.primary-button3 {
  width: 100px;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  cursor: pointer;
  text-align: center;
  font-size: 23px;
  color: #fff;
  font-family: "IBM Plex Sans Thai";
}
@media screen and (max-width: 450px) {
  .book-now1 {
    font-size: 18px;
  }
}
