.arcticonsgoogle-pay1,
.logosgoogle-pay-icon {
  height: 104px;
  width: 136px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 1;
}
.arcticonsgoogle-pay1 {
  height: 86px;
  width: 75px;
}
.logos-inner,
.rectangle-parent2 {
  display: flex;
  align-items: flex-start;
}
.rectangle-parent2 {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #d9d9d9;
  border: 1px solid #fff;
  flex-direction: row;
  justify-content: flex-start;
  padding: 30px 14px 9px 31px;
  gap: 6px;
}
.logos-inner {
  flex-direction: column;
  justify-content: flex-end;
  padding: 0 13px 4px 0;
}
.payment-logos-child {
  height: 147px;
  width: 264px;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #d9d9d9;
  border: 1px solid #fff;
  box-sizing: border-box;
  display: none;
}
.logospaypal-icon,
.vector-icon1 {
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.logospaypal-icon {
  height: 92px;
  width: 52px;
  flex-shrink: 0;
}
.vector-icon1 {
  align-self: stretch;
  flex: 1;
  max-width: 100%;
  max-height: 100%;
}
.payment-logos,
.visa-logo {
  box-sizing: border-box;
  display: flex;
}
.visa-logo {
  height: 75px;
  width: 102px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 6px;
}
.payment-logos {
  width: 264px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #d9d9d9;
  border: 1px solid #fff;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 26px 34px 25px;
  gap: 27px;
}
.logosvisa-icon {
  height: 60px;
  width: 176px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 1;
}
.payment-logos1 {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #d9d9d9;
  border: 1px solid #fff;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 45px 41px 38px 45px;
}
.simple-iconsapplepay {
  height: 110px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  z-index: 1;
}
.payment-logos2 {
  width: 264px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #d9d9d9;
  border: 1px solid #fff;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 14px 42px 19px 49px;
}
.footer,
.logos {
  display: flex;
  flex-direction: row;
  justify-content: center;
  max-width: 100%;
}
.logos {
  align-items: flex-end;
  gap: 59px;
}
.footer {
  width: 1426px;
  align-items: flex-start;
  padding: 0 20px;
  box-sizing: border-box;
}
@media screen and (max-width: 1425px) {
  .logos {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 825px) {
  .logos {
    gap: 29px;
  }
}
@media screen and (max-width: 450px) {
  .payment-logos1,
  .payment-logos2 {
    padding-left: 20px;
    box-sizing: border-box;
  }
  .payment-logos2 {
    padding-right: 20px;
  }
}
