.theatre-name {
  text-decoration: none;
  flex: 1;
  position: relative;
  color: inherit;
  display: inline-block;
  max-width: 100%;
  z-index: 1;
}
.theatre-name-wrapper {
  width: 337px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 3px;
  box-sizing: border-box;
  max-width: 100%;
}
.movie-name {
  width: 241px;
  height: 80px;
  position: relative;
  display: inline-block;
  flex-shrink: 0;
  z-index: 2;
}
.frame-item {
  height: 88px;
  width: 192px;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 45px;
  background-color: #0c0908;
  border: 1px solid #fff;
  box-sizing: border-box;
  display: none;
  z-index: 1;
}
.wed-12 {
  height: 66px;
  width: 160px;
  position: relative;
  font-weight: 600;
  display: inline-block;
  flex-shrink: 0;
  z-index: 3;
}
.vector-icon {
  width: 23.6px;
  height: 12px;
  position: relative;
  z-index: 2;
}
.calendar-navigation,
.rectangle-parent,
.showtime-calendar {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.calendar-navigation {
  flex-direction: column;
  padding: 16px 0 0;
  margin-left: -29px;
}
.rectangle-parent,
.showtime-calendar {
  box-sizing: border-box;
}
.rectangle-parent {
  width: 192px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 45px;
  background-color: #0c0908;
  border: 1px solid #fff;
  flex-direction: row;
  padding: 20px 16px 0;
  z-index: 1;
}
.showtime-calendar {
  flex: 1;
  flex-direction: column;
  padding: 5px 0 0;
  min-width: 182px;
  font-size: 34px;
}
.time-slot {
  position: absolute;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25) inset;
  border-radius: 20px;
  background-color: #00f;
  border: 1px solid #fff;
  box-sizing: border-box;
  width: 193px;
  z-index: 1;
}
.am,
.d {
  margin: 0;
}
.d {
  white-space: pre-wrap;
}
.am-2d {
  position: absolute;
  top: 13px;
  left: 28px;
  font-weight: 600;
  display: inline-block;
  width: 204px;
  height: 60px;
  flex-shrink: 0;
  z-index: 2;
}
.time-selection {
  height: 80px;
  flex: 1;
  position: relative;
}
.day-selection,
.showtime-day-container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.day-selection {
  width: 252px;
  flex-direction: row;
  padding: 0 20px 0 0;
}
.showtime-day-container {
  flex: 1;
  flex-direction: column;
  padding: 7px 0 0;
  min-width: 183px;
}
.frame-inner {
  position: absolute;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25) inset;
  border-radius: 20px;
  background-color: #212122;
  border: 1px solid #fff;
  box-sizing: border-box;
  width: 193px;
  z-index: 1;
}
.pm-3d {
  position: absolute;
  height: calc(100% - 20px);
  top: 12px;
  left: 33px;
  font-weight: 600;
  display: inline-block;
  width: 249px;
  flex-shrink: 0;
  z-index: 2;
}
.showtime-day-container-inner,
.showtime-day-container1 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.showtime-day-container-inner {
  align-self: stretch;
  flex-direction: row;
}
.showtime-day-container1 {
  flex: 1;
  flex-direction: column;
  padding: 5px 0 0;
  box-sizing: border-box;
  min-width: 183px;
}
.pm-2d {
  position: absolute;
  top: 13px;
  left: 37px;
  font-weight: 600;
  display: inline-block;
  width: 204px;
  height: 60px;
  z-index: 2;
}
.showtime-options-inner {
  flex: 0.8638;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 38px 0 0;
  box-sizing: border-box;
  min-width: 181px;
}
.frame-child1 {
  position: absolute;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25) inset;
  border-radius: 20px;
  background-color: rgba(33, 33, 34, 0.9);
  border: 1px solid #fff;
  box-sizing: border-box;
  width: 193px;
  z-index: 1;
}
.pm-3d1 {
  position: absolute;
  height: calc(100% - 22px);
  top: 13px;
  left: 52px;
  font-weight: 600;
  display: inline-block;
  width: 161px;
  z-index: 2;
}
.showtime-day-container2 {
  width: 213px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.frame-parent1,
.movie-name-parent,
.showtime-options,
.showtime-selection {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.showtime-options {
  margin: 0;
  flex: 1;
  flex-direction: row;
  gap: 11px;
  white-space: nowrap;
  text-align: left;
  font-size: 24px;
  color: #fff;
  font-family: Inter;
}
.frame-parent1,
.movie-name-parent,
.showtime-selection {
  align-self: stretch;
}
.showtime-selection {
  flex-direction: row;
  padding: 0 0 0 3px;
  box-sizing: border-box;
  margin-top: -2px;
}
.frame-parent1,
.movie-name-parent {
  flex-direction: column;
}
.frame-parent1 {
  gap: 39px;
  text-align: left;
  font-size: 34px;
  color: #fff;
  font-family: Inter;
}
@media screen and (max-width: 1200px) {
  .showtime-options {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1050px) {
  .movie-name,
  .theatre-name,
  .wed-12 {
    font-size: 27px;
  }
}
@media screen and (max-width: 750px) {
  .frame-parent1 {
    gap: 19px;
  }
}
@media screen and (max-width: 450px) {
  .movie-name,
  .theatre-name,
  .wed-12 {
    font-size: 20px;
  }
  .am-2d,
  .pm-2d,
  .pm-3d {
    font-size: 19px;
  }
  .showtime-options-inner {
    flex: 1;
  }
  .pm-3d1 {
    font-size: 19px;
  }
}
