.arcticonsgoogle-pay {
  position: absolute;
  top: 3px;
  left: 65px;
  width: 48px;
  height: 48px;
  overflow: hidden;
}
.pay,
.pay-button-background {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}
.pay-button-background {
  right: 0;
  bottom: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: red;
  border: 1px solid #fff;
  box-sizing: border-box;
  z-index: 1;
}
.pay {
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.primary-button4 {
  align-self: stretch;
  height: 54px;
  position: relative;
  cursor: pointer;
  text-align: center;
  font-size: 32px;
  color: #fff;
  font-family: "IBM Plex Sans Thai";
}
@media screen and (max-width: 825px) {
  .pay {
    font-size: 26px;
  }
}
@media screen and (max-width: 450px) {
  .pay {
    font-size: 19px;
  }
}
