.seating-layout {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.booking {
  width: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 15px 0 0 36px;
  box-sizing: border-box;
  gap: 519px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 750px) {
  .booking {
    gap: 259px;
  }
}
@media screen and (max-width: 450px) {
  .booking {
    gap: 130px;
  }
}
