.button-shape {
  height: 77px;
  flex: 1;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: #d3d3d3;
  border: 1px solid #fff;
  box-sizing: border-box;
  max-width: 100%;
}
.ggprofile-icon,
.username {
  position: relative;
  flex-shrink: 0;
  display: none;
}
.username {
  align-self: stretch;
  width: 1162px;
  align-items: center;
  justify-content: center;
  max-width: 100%;
}
.ggprofile-icon {
  height: 52px;
  width: 62px;
  overflow: hidden;
}
.primary-button {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
  height: 100%;
  z-index: 1;
  text-align: center;
  font-size: 30px;
  color: #363636;
  font-family: "Inria Sans";
}
