.image-13-icon {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.wandavision {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
  z-index: 1;
}
.vision-and-wanda {
  margin: 0;
}
.vision-and-wanda-container {
  margin: 0;
  position: relative;
  font-size: 32px;
  font-weight: 500;
  font-family: Inter;
  z-index: 1;
}
.wandavision-parent {
  flex-direction: column;
  gap: 11px;
  max-width: 100%;
}
.content,
.primary-button-wrapper,
.wandavision-parent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.primary-button-wrapper {
  width: 196px;
  flex-direction: row;
  padding: 0 48px;
  box-sizing: border-box;
}
.content {
  flex-direction: column;
  gap: 22px;
  max-width: 100%;
}
.placeholder-icon {
  width: 201px;
  flex: 1;
  position: relative;
  border-radius: 30px;
  max-height: 100%;
  overflow: hidden;
  object-fit: cover;
  cursor: pointer;
  z-index: 1;
}
.placeholder-wrapper {
  height: 313px;
  width: 278px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 14px;
  box-sizing: border-box;
  flex-shrink: 0;
}
.placeholder-icon1 {
  width: 275px;
  flex: 1;
  position: relative;
  border-radius: 30px;
  max-height: 100%;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}
.placeholder-container {
  height: 417px;
  width: 352px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 12px;
  box-sizing: border-box;
  max-width: 100%;
  flex-shrink: 0;
}
.placeholder-icon2 {
  margin-left: -21px;
  margin-bottom: -20px;
  height: 342px;
  flex: 1;
  position: relative;
  border-radius: 50px;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
}
.placeholder {
  align-self: stretch;
  height: 299px;
  border-radius: 45px;
  border: 1px solid #fff;
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  cursor: pointer;
  z-index: 1;
}
.placeholder,
.placeholder-frame,
.wv {
  display: flex;
  box-sizing: border-box;
}
.placeholder-frame {
  width: 201px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 12px;
  flex-shrink: 0;
}
.wv {
  width: 100%;
  position: relative;
  background-color: #fff;
  overflow: hidden;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 541px 37px 66px;
  gap: 14px;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  font-size: 60px;
  color: #fff;
  font-family: "IBM Plex Sans Thai";
}
@media screen and (max-width: 1350px) {
  .wv {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 800px) {
  .wandavision {
    font-size: 48px;
  }
  .vision-and-wanda-container {
    font-size: 26px;
  }
}
@media screen and (max-width: 450px) {
  .wandavision {
    font-size: 36px;
  }
  .vision-and-wanda-container {
    font-size: 19px;
  }
}
