.show-details-child {
  height: 299px;
  flex: 1;
  position: relative;
  border-radius: 30px;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  flex-shrink: 0;
  cursor: pointer;
  z-index: 1;
}
.show-details {
  align-self: stretch;
  height: 291px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 44px 0 37px;
  box-sizing: border-box;
}
.p {
  margin: 0;
}
.fear-the-walking-container1 {
  align-self: stretch;
  height: 69px;
  position: relative;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  z-index: 3;
}
.shows {
  position: absolute;
  top: 0;
  left: 262px;
  width: 282px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: center;
  font-size: 24px;
  color: #fff;
  font-family: "Inria Sans";
}
@media screen and (max-width: 450px) {
  .fear-the-walking-container1 {
    font-size: 19px;
  }
}
