.wonder-woman-1984 {
  align-self: stretch;
  position: relative;
  line-height: 86px;
  backdrop-filter: blur(4px);
  text-shadow: 1px 0 0#fff, 0 1px 0#fff, -1px 0 0#fff, 0-1px 0#fff;
  z-index: 1;
}
.wonder-woman-finds {
  margin: 0;
}
.wonder-woman-finds-container {
  flex: 1;
  position: relative;
  font-weight: 500;
  display: inline-block;
  text-shadow: 1px 0 0#fff, 0 1px 0#fff, -1px 0 0#fff, 0-1px 0#fff;
  max-width: 100%;
  z-index: 1;
}
.wonder-woman-finds-herself-bat-wrapper {
  align-self: stretch;
  flex-direction: row;
  padding: 0 7px 0 8px;
  box-sizing: border-box;
  max-width: 100%;
  font-size: 30px;
  font-family: Inter;
}
.primary-button-frame,
.wonder-woman-1984-parent,
.wonder-woman-finds-herself-bat-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.wonder-woman-1984-parent {
  align-self: stretch;
  flex-direction: column;
  gap: 5px;
  max-width: 100%;
}
.primary-button-frame {
  width: 216px;
  flex-direction: row;
  padding: 0 58px;
  box-sizing: border-box;
}
.frame-container,
.frame-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 100%;
}
.frame-container {
  align-self: stretch;
  justify-content: flex-start;
  gap: 25px;
}
.frame-wrapper {
  flex: 1;
  justify-content: flex-end;
  padding: 0 0 21px;
  box-sizing: border-box;
}
.frame-child {
  height: 405px;
  flex: 1;
  position: relative;
  border-radius: 50px;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
}
.frame-div,
.frame-group {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-shrink: 0;
}
.frame-div {
  width: 275px;
  border-radius: 45px;
  border: 1px solid #000;
  box-sizing: border-box;
  overflow: hidden;
  align-items: flex-start;
  min-width: 275px;
  z-index: 1;
}
.frame-group {
  width: 938px;
  align-items: flex-end;
  gap: 8px;
  max-width: 100%;
}
.image-8-icon {
  position: absolute;
  top: 0;
  left: -46px;
  width: 1829px;
  height: 1860px;
  object-fit: cover;
}
.card-img-icon,
.image-container-child {
  position: absolute;
  width: 201px;
  height: 299px;
  object-fit: cover;
  z-index: 1;
}
.image-container-child {
  top: 654px;
  left: 1464px;
  border-radius: 30px;
  overflow: hidden;
}
.card-img-icon {
  top: 651px;
  left: 1764px;
  border-radius: 45px;
}
.frame-icon,
.image-container-child1,
.image-container-inner,
.image-container-item {
  position: absolute;
  top: 651px;
  left: 1456px;
  border-radius: 30px;
  width: 201px;
  height: 299px;
  overflow: hidden;
  object-fit: cover;
  z-index: 2;
}
.frame-icon,
.image-container-child1,
.image-container-inner {
  top: 655px;
  left: 1474px;
  z-index: 3;
}
.frame-icon,
.image-container-child1 {
  top: 661px;
  left: 1078px;
  cursor: pointer;
  z-index: 1;
}
.image-container-child1 {
  top: 651px;
  left: 1363px;
  z-index: 4;
}
.image-container {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.card-img-icon1 {
  align-self: stretch;
  flex: 1;
  position: relative;
  border-radius: 45px;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  flex-shrink: 0;
  z-index: 2;
}
.card-img-wrapper,
.ww1984 {
  display: flex;
  box-sizing: border-box;
}
.card-img-wrapper {
  height: 311px;
  width: 201px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 12px;
  flex-shrink: 0;
}
.ww1984 {
  width: 100%;
  position: relative;
  background-color: #fff;
  overflow: hidden;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 561px 45px 58px;
  gap: 822px;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  font-size: 60px;
  color: #000;
  font-family: "IBM Plex Sans Thai";
}
@media screen and (max-width: 1350px) {
  .ww1984 {
    flex-wrap: wrap;
    gap: 411px;
    padding-left: 22px;
    padding-right: 22px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .wonder-woman-1984 {
    font-size: 48px;
    line-height: 69px;
  }
  .wonder-woman-finds-container {
    font-size: 24px;
  }
  .frame-wrapper {
    min-width: 100%;
  }
  .frame-child,
  .frame-div {
    flex: 1;
  }
  .frame-group {
    flex-wrap: wrap;
  }
  .ww1984 {
    gap: 205px;
  }
}
@media screen and (max-width: 450px) {
  .wonder-woman-1984 {
    font-size: 36px;
    line-height: 52px;
  }
  .wonder-woman-finds-container {
    font-size: 18px;
  }
  .ww1984 {
    gap: 103px;
  }
}
