.image-12-icon {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  bottom: 0;
  left: calc(50% - 725px);
  max-height: 100%;
  object-fit: cover;
}
.fear-the-walking {
  margin: 0;
}
.blank-line {
  margin: 0;
  color: #000;
}
.fear-the-walking-container {
  margin: 0;
  align-self: stretch;
  height: 126px;
  position: relative;
  font-size: inherit;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  flex-shrink: 0;
  z-index: 1;
}
.the-lives-of-container {
  position: relative;
  font-weight: 500;
  z-index: 1;
}
.fear-the-walking-dead-parent,
.the-lives-of-the-residents-of-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.the-lives-of-the-residents-of-wrapper {
  width: 616px;
  flex-direction: row;
  padding: 0 6px;
  box-sizing: border-box;
  margin-top: -34px;
  font-size: 30px;
  font-family: Inter;
}
.fear-the-walking-dead-parent {
  align-self: stretch;
  flex-direction: column;
}
.primary-button-container {
  flex-direction: row;
  align-items: flex-start;
  padding: 0 70px;
}
.ftwd,
.primary-button-container,
.show-info {
  display: flex;
  justify-content: flex-start;
}
.show-info {
  width: 665px;
  flex-direction: column;
  align-items: flex-start;
  gap: 26px;
  min-width: 665px;
  max-width: 100%;
}
.ftwd {
  position: relative;
  background-color: #fff;
  width: 100%;
  overflow: hidden;
  flex-direction: row;
  align-items: flex-end;
  padding: 516px 0 86px 27px;
  box-sizing: border-box;
  line-height: normal;
  letter-spacing: normal;
  row-gap: 20px;
  text-align: left;
  font-size: 60px;
  color: #fff;
  font-family: "IBM Plex Sans Thai Looped";
}
@media screen and (max-width: 1350px) {
  .show-info {
    flex: 1;
  }
  .ftwd {
    flex-wrap: wrap;
    padding-left: 20px;
    padding-right: 20px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 800px) {
  .fear-the-walking-container {
    font-size: 48px;
  }
  .the-lives-of-container {
    font-size: 24px;
  }
  .show-info {
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .fear-the-walking-container {
    font-size: 36px;
  }
  .the-lives-of-container {
    font-size: 18px;
  }
}
