.book-now2,
.button-shape2 {
  position: absolute;
  margin: 0 !important;
}
.button-shape2 {
  height: 50px;
  width: 150px;
  bottom: -13px;
  left: calc(50% - 75px);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 75px;
  background-color: red;
  border: 1px solid #fff;
  box-sizing: border-box;
}
.book-now2 {
  height: 100%;
  width: 145px;
  top: 0;
  left: -23px;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.primary-button5 {
  height: 25px;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  cursor: pointer;
  z-index: 1;
  text-align: center;
  font-size: 23px;
  color: #fff;
  font-family: "IBM Plex Sans Thai";
}
@media screen and (max-width: 450px) {
  .book-now2 {
    font-size: 18px;
  }
}
