.search {
  margin: 0;
  position: absolute;
  height: calc(100% - 12px);
  top: 4px;
  left: 34px;
  font-size: 36px;
  font-weight: 400;
  font-family: inherit;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 236px;
  white-space: nowrap;
  z-index: 2;
}
.mingcutesearch-line-icon {
  position: absolute;
  height: calc(100% - 16px);
  top: 8px;
  bottom: 8px;
  left: 28px;
  max-height: 100%;
  width: 66px;
  overflow: hidden;
  z-index: 2;
}
.search-bar-container {
  height: 77px;
  width: 1162px;
  position: relative;
  max-width: 100%;
}
.left-pane {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 20px;
  box-sizing: border-box;
  max-width: 100%;
  text-align: center;
  font-size: 30px;
  color: #363636;
  font-family: "Inria Sans";
}
