.image-15-icon {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.phstar-fill-icon,
.phstar-fill-icon1,
.phstar-fill-icon2 {
  width: 19px;
  height: 23px;
  position: absolute;
  margin: 0 !important;
  bottom: 424px;
  left: 92px;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 1;
}
.phstar-fill-icon1,
.phstar-fill-icon2 {
  bottom: 429px;
  left: 357px;
}
.phstar-fill-icon2 {
  bottom: 426px;
  left: 617px;
}
.now-playing {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 700;
  font-family: inherit;
  z-index: 1;
}
.desktop-2,
.playing-container,
.right-pane {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.playing-container {
  width: 344px;
  flex-direction: row;
  padding: 0 54px;
  max-width: 100%;
}
.desktop-2,
.right-pane {
  flex-direction: column;
}
.right-pane {
  width: 825px;
  padding: 0 20px 0 0;
  gap: 20px;
  max-width: 100%;
}
.desktop-2 {
  width: 100%;
  position: relative;
  background-color: #fff;
  overflow: hidden;
  padding: 58px 9px 369px;
  gap: 46px;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  font-size: 36px;
  color: #fff;
  font-family: "Inria Sans";
}
@media screen and (max-width: 750px) {
  .now-playing {
    font-size: 29px;
  }
  .desktop-2 {
    gap: 23px;
  }
}
@media screen and (max-width: 450px) {
  .now-playing {
    font-size: 22px;
  }
  .playing-container {
    padding-left: 20px;
    padding-right: 20px;
    box-sizing: border-box;
  }
}
