.screen-12 {
  margin: 0;
}
.booking-status {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 20px 0 32px;
}
.empty {
  align-self: stretch;
  position: relative;
  z-index: 1;
}
.seat-status-options {
  width: 181px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 5px 0 0;
  box-sizing: border-box;
}
.booked {
  width: 181px;
  position: relative;
  display: inline-block;
  z-index: 1;
}
.seat-status-options1 {
  width: 213px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 5px 20px 0 0;
  box-sizing: border-box;
}
.selected {
  width: 181px;
  position: relative;
  display: inline-block;
  flex-shrink: 0;
  z-index: 1;
}
.seat-status-legend {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: 20px;
}
.seat-status,
.seat-status-container {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.seat-status {
  width: 879px;
  flex-direction: column;
  justify-content: flex-start;
  gap: 38px;
}
.seat-status-container {
  width: 1159px;
  flex-direction: row;
  justify-content: center;
  padding: 0 20px;
  box-sizing: border-box;
}
.price-indicator {
  height: 273px;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  right: 0;
  bottom: -162px;
  left: 0;
  border-radius: 75px;
  background-color: #d9d9d9;
  z-index: 1;
}
.rs560 {
  width: 236px;
  position: relative;
  display: inline-block;
  flex-shrink: 0;
  text-shadow: 1px 0 0#fff, 0 1px 0#fff, -1px 0 0#fff, 0-1px 0#fff;
  z-index: 2;
}
.payment-button-container {
  width: 179px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 2px 0 0;
  box-sizing: border-box;
  text-align: center;
  font-size: 24px;
  color: #fff;
  font-family: "IBM Plex Sans Thai";
}
.payment-details {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  padding: 28px 84px 25px 63px;
  position: relative;
  gap: 20px;
  font-size: 40px;
  color: red;
}
.selection-details {
  width: 1177px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 5px;
  max-width: 100%;
}
.booking-summary {
  width: 1361px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 20px;
  box-sizing: border-box;
  max-width: 100%;
  text-align: left;
  font-size: 34px;
  color: #fff;
  font-family: Inter;
}
@media screen and (max-width: 1200px) {
  .payment-details {
    padding-left: 31px;
    padding-right: 42px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .booked,
  .empty,
  .selected {
    font-size: 27px;
  }
  .rs560 {
    font-size: 32px;
  }
}
@media screen and (max-width: 750px) {
  .payment-details,
  .seat-status-legend {
    flex-wrap: wrap;
    justify-content: center;
  }
  .payment-details {
    padding-right: 21px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .booked,
  .empty,
  .selected {
    font-size: 20px;
  }
  .seat-status {
    gap: 19px;
  }
  .rs560 {
    font-size: 24px;
  }
}
